input {
  padding-left: 6px;
  height: 35px;
  margin-right: 26px;
}

input:hover {
    padding-left: 6px;
    height: 35px;
    margin-right: 26px;
    outline: none;
  }

  input:active {
    padding-left: 6px;
    height: 35px;
    margin-right: 26px;
    outline: none;
  }
.btnSClass:hover {
  outline: none;
  opacity: 0.7;
}
.btnSClass:active {
  outline: none;
  opacity: 0.9;
}

